  methods: {
    filterProducts() {
      console.log(this.categoryOpen);
      console.log(this.categoryActive);
      console.log(this.sizeSelectedItems);

      if (this.categoryOpen.length > 0 && this.categoryActive.length > 0) {
        let categoriesFilter = this.items[1].children.filter((item) => {
          return item.id == this.categoryActive;
        });
        let categoryFilter = categoriesFilter[0];
        console.log(categoryFilter);
        this.products = this.productsOrg.filter((item) => {
          return item.type == categoryFilter.name;
        });
      } else {
        this.products = [...this.productsOrg];
      }

      // filter by size
      if (this.sizeSelectedItems.length > 0) {
        this.products = this.products.filter((item) => {
          return this.sizeSelectedItems.some((filterSize) => {
            return item.size.includes(filterSize);
          });
        });
      }
    },
  },
